.nav-bar li {
    float: left;
  }
  
  .nav-bar  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 25px;
    text-decoration: none;
  }
  
  .nav-bar li a:hover {
    background-color: #19256c;
    border-radius: 8px;
  }
  .nav-bar  ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #3F51B5;
}